
/* ============================================================================
   HOME COMPONENT STYLES
   ============================================================================ */

/* Thumbnail Icon Styles */
.thumbnail-location::before {
  content: '📍';
  display: inline-block;
  margin-right: 2px;
  vertical-align: middle;
}

.thumbnail-characters::before {
  content: '👥';
  display: inline-block;
  margin-right: 2px;
  vertical-align: middle;
}

.thumbnail-stories::before {
  content: '📖';
  display: inline-block;
  margin-right: 2px;
  vertical-align: middle;
}

.thumbnail-date::before {
  content: '📅';
  display: inline-block;
  margin-right: 2px;
  vertical-align: middle;
}

.thumbnail-tags::before {
  content: '🏷️';
  display: inline-block;
  margin-right: 2px;
  vertical-align: middle;
}

/* Thumbnail Components */
.thumbnail-tags {
  width: fit-content;
  margin: 1px;
  padding: 3px 8px 3px 3px;
  font-size: x-small;
  font-style: italic;
  color: var(--b2-color);
  border: 1px solid var(--g3-color);
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  
  &:hover {
    /* background-color: var(--g3-color); */
    color: var(--g1-color);
    box-shadow: 2px 2px 5px var(--shadow-color);
    transform: translateY(-1px);
  }
}

.world-thumbnail {
  background: var(--g4-color);
  border-radius: 15px;
  margin: 5px;
  height: fit-content;
  padding: 10px;
  transition: all 0.3s ease-in-out;
  
  &:hover {
    box-shadow: 2px 2px 15px var(--shadow-color);

    &.event {
      box-shadow: 2px 2px 5px var(--event-color) !important;
    }

    &.location {
      box-shadow: 2px 2px 5px var(--location-color) !important;
    }

    &.character {
      box-shadow: 2px 2px 5px var(--character-color) !important;
    }

    &.story {
      box-shadow: 2px 2px 5px var(--story-color) !important;
    }
  }
}

.thumbnail-info {
  margin: 0;
  padding: 0;
  font-size: x-small;
  color: var(--g1-color);
  /* background-color: red; */
}


.thumbnail-description { 
  margin-top: 5px;
  margin-bottom: 5px;
  font-size: x-small;
  color: var(--g1-color);
  line-height: 1.4;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.thumbnail-header {
  display: flex;
  font-size: small;
  justify-content: space-between;
  align-items: center;
}

.thumbnail-title {
  margin: 0;
  font-size: small;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 70%;
}

.thumbnail-delete-button {
  background: var(--b4-color);
  color: var(--g5-color);
  border: none;
  border-radius: 8px;
  padding: 5px;
  margin-inline: 5px;
  font-size: small;
  transition: all 0.3s ease-in-out;

  &:hover {
    background-color: var(--b3-color);
    /* box-shadow: var(--b1-color) 2px 2px 10px; */
  }
}

.thumbnail-tag-container {
  display: flex;
  flex-wrap: wrap;
}

.thumbnail-info {
  margin: 5px 0;
  font-size: x-small;
  color: var(--g2-color);
  line-height: 1.3;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

.thumbnail-info strong {
  color: var(--g1-color);
  font-weight: bold;
}